import React from 'react';
import 'antd/dist/antd.css';
import { Form, Input, Button, Result, message } from 'antd';
import { SmileOutlined } from '@ant-design/icons';
import axios from 'axios';

class WelcomeSignup extends React.Component {
    constructor(props) {
        super(props);
    }

    send_regist(value) {
        axios.post('/welcome_api',
            {
                api: 'create_user',
                ...value
            }).then(data => {
                console.log(data.data)
                if (data.data.status === 0) {
                    this.props.onNext();
                } else {
                    message.info('');
                }
            });
    }

    render() {
        const layout = {
            labelCol: { span: 8 },
            wrapperCol: { span: 8 },
        };
        const tailLayout = {
            wrapperCol: { offset: 8, span: 16 },
        };
        return (
            <Result
                icon={<SmileOutlined />}
                title=""
                extra={<Form
                    {...layout}
                    name="basic"
                    onFinish={(value) => this.send_regist(value)}
                >
                    <Form.Item
                        label=""
                        name="username"
                        rules={[{ required: true, message: '' }]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        label=""
                        name="password"
                        rules={[{ required: true, message: '' }]}
                    >
                        <Input.Password />
                    </Form.Item>
                    <Form.Item
                        name="confirm"
                        label=""
                        dependencies={['password']}
                        hasFeedback
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                            ({ getFieldValue }) => ({
                                validator(rule, value) {
                                    if (!value || getFieldValue('password') === value) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject('');
                                },
                            }),
                        ]}
                    >
                        <Input.Password />
                    </Form.Item>
                    <Form.Item {...tailLayout}>
                        <Button type="primary" htmlType="submit">
                            
                        </Button>
                    </Form.Item>
                </Form>}
            />

        );
    }
}


export default function welcome_2(props) {
    return (
        <WelcomeSignup onNext={() => props.onNext()} />
    );
}